import React from "react";
import { Helmet } from "react-helmet-async";
const keywords =
  "nzvoslov, chemick nzvoslov, chemick nzvoslov procviovn, procviovn nzv prvk, procviovn nzvoslov slouenin, anorganick nzvoslov, nzvoslov anorganickch slouenin, procviovn nzvoslov, nazvoslovi, procviovn nzvoslov";

function StandardHelmetTemplate({ title, description }) {
  return (
    <Helmet>
      {/* Standard metadata tags */}
      <title>{title}</title>
      <meta name="description" content={description} />
      <meta name="keywords" content={keywords} />
      <meta name="author" content="Vclav Jirka" />
      {/* Facebook tags */}
      <meta property="og:type" content="website" />
      <meta property="og:url" content="https://nazvoslovi.eu/" />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:locale" content="cs_CZ" />
      <meta
        property="og:image"
        content={"/static/images/nazvoslovi_og_logo.png"}
      />
      {/* Twitter tags */}
      <meta name="twitter:creator" content="Vclav Jirka" />
      <meta name="twitter:url" content="https://nazvoslovi.eu/" />
      <meta name="twitter:card" content="website" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta
        property="twitter:image"
        content={"/static/images/nazvoslovi_og_logo.png"}
      />
    </Helmet>
  );
}

export default StandardHelmetTemplate;
